
import XHeader from 'vux/src/components/x-header/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
XHeader,
ViewBox,
Flexbox,
FlexboxItem
},
data() {
return {
model: 1,
order_list: [],
two: [],
order_state: '',
state: [{
state: '0',
title: "未支付",
type: "info"
},
{
state: '1',
title: "未发货",
type: "warning"
},
{
state: '2',
title: "已发货",
type: ""
},
{
state: '3',
title: "已签收",
type: "success"
},
{
state: '4',
title: "退款/售后",
type: "danger"
}
]
}
},
mounted() {
if(this.$route.params["order_number"] == "1") {

this.model = 2;
};
if(this.$route.params["order_number"] == "2") {

this.model = 3;
};
if(this.$route.params["order_number"] == "3") {

this.model = 4;
};
if(this.$route.params["order_number"] == "4") {

this.model = 5;
};

this.$get(
"order/getList", {},
res => {
this.order_list = res.msg;
console.log(this.order_list);
}
);

},
methods: {
setModel: function(type) {
this.model = type;
},

toOrderInfo(group) {
var order_id = group.order_id
console.log("uuu", group)
this.$router.push({
path: "/orderInfo/orderInfo",
name: "/orderInfo/orderInfo",
params: {
order_id: order_id
}
});
},
}
}



